h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video,
input {
  margin: 0;
  padding: 0; }

/*
HTML5 propaty*/
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
small {
  display: block; }

/*
Reset: border propaty*/
img,
fieldset {
  border: 0; }

/*
Reset: List selector*/
ol,
ul,
li {
  list-style: none; }

/*
Reset: font propaty*/
address {
  font-style: normal;
  font-weight: normal; }

/*
Reset: horizontal rule display*/
hr {
  display: none; }

/*
Reset: link display*/
a {
  outline-style: none; }

a img {
  border: none; }

*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box; }

