/// Makes an element's :before pseudoelement a FontAwesome icon.
/// @param {string} $content Optional content value to use.
/// @param {string} $where Optional pseudoelement to target (before or after).
@mixin icon($content: false, $where: before) {
  text-decoration: none;

  &:#{$where} {
    @if $content {
      content: $content;
    }

    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    font-family: FontAwesome;
    font-style: normal;
    font-weight: normal;
    text-transform: none !important;
  }
}

/// Applies padding to an element, taking the current element-margin value into account.
/// @param {mixed} $tb Top/bottom padding.
/// @param {mixed} $lr Left/right padding.
/// @param {list} $pad Optional extra padding (in the following order top, right, bottom, left)
/// @param {bool} $important If true, adds !important.
@mixin padding($tb, $lr, $pad: (0, 0, 0, 0), $important: null) {
  @if $important {
    $important: "!important";
  }

  $x: 0.1em;

  @if unit(_size(element-margin)) == "rem" {
    $x: 0.1rem;
  }

  padding: ($tb + nth($pad, 1)) ($lr + nth($pad, 2))
    max($x, $tb - _size(element-margin) + nth($pad, 3)) ($lr + nth($pad, 4)) #{$important};
}

/// Encodes a SVG data URL so IE doesn't choke (via codepen.io/jakob-e/pen/YXXBrp).
/// @param {string} $svg SVG data URL.
/// @return {string} Encoded SVG data URL.
@function svg-url($svg) {
  $svg: str-replace($svg, '"', "'");
  $svg: str-replace($svg, "%", "%25");
  $svg: str-replace($svg, "<", "%3C");
  $svg: str-replace($svg, ">", "%3E");
  $svg: str-replace($svg, "&", "%26");
  $svg: str-replace($svg, "#", "%23");
  $svg: str-replace($svg, "{", "%7B");
  $svg: str-replace($svg, "}", "%7D");
  $svg: str-replace($svg, ";", "%3B");

  @return url("data:image/svg+xml;charset=utf8,#{$svg}");
}

// header height values
@mixin header-height() {
  height: $header-height;
  @include breakpoint("<=small") {
    height: $header-height-sp;
  }
  @include breakpoint(">=xlarge") {
    height: $header-height-xl;
  }
}

@mixin header-top-padding() {
  padding-top: $header-height;
  @include breakpoint(">=xlarge") {
    padding-top: $header-height-xl;
  }
  @include breakpoint("<=small") {
    padding-top: $header-height-sp;
  }
}

@mixin header-line-height() {
  line-height: $header-height;
  @include breakpoint(">=xlarge") {
    line-height: $header-height-xl;
    @include breakpoint("<=small") {
      line-height: $header-height-sp;
    }
  }
}

// font-size
@mixin size-header() {
  font-size: 50px;
  line-height: 50px;
  @include breakpoint("<=small") {
    font-size: 30px;
    line-height: 30px;
  }
}

@mixin size-menu() {
  font-size: 20px;
  @include breakpoint("small") {
    font-size: 20px;
  }
  @include breakpoint("<=xsmall") {
    // < 360
    font-size: 16px;
  }
}

@mixin size-caption() {
  font-size: 20px;
}
